.tags{
  padding: 20px 36px;
  display: none;
  &_active{
    display: inherit;
  }
  @media only screen and (max-width: 1024px) {
    display: none;
  }
}
.tagTitle{
  margin-bottom: 20px;
}
.tagsWrapper{
  display: flex;
  flex-wrap: wrap;
  & > div{
    margin: 0 11px 20px 0;
  }
}