.wrapper{
  height: 1400px;
  background: linear-gradient(180deg, rgba(185, 251, 255, 0.2) 0%, rgba(227, 220, 255, 0.2) 100%);
  backdrop-filter: blur(30px);
  transition: width 1s;
  width: 98px;
  &_active{
    width: 360px;
    transition: 1s;
    flex: 0 0 360px;
  }
  @media only screen and (max-width: 1024px) {
    width: 100%;
    height: initial;
    background: none;
    backdrop-filter: none;
    &_active{
      flex: initial;
    }
  }

}


