@import "../../styles/variables";
.wrapper{
    display: flex;
    justify-content: space-between;
    padding: 18px 86px 19px 90px;
    flex: 1 0 auto;
    background: linear-gradient(180deg, rgba(185, 251, 255, 0.2) 0%,
                rgba(227, 220, 255, 0.2) 100%), linear-gradient(180deg, rgba(255, 255, 255, 0.2) 0%,
                rgba(255, 255, 255, 0) 100%), rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(20px);
    transition: padding-left 1s;
    position: sticky;
    top: 0;
    z-index: 5;
    &_active{
        padding-left: 20px;
    }
    @media only screen and (max-width: 1024px) {
        display: none;
    }
}
.stateWrap{
    display: flex;
    align-items: center;
}
.cloudImg{
    width: 18px;
    height: 16px;
    margin-right: 10px;
    background: url("../../img/header/cloud.svg");
}
.title{
    color: $another-grey-light;
    margin-right: 5px;
}
.state{
    color: $green;
    margin-left: 3px;
}
.error{
    color: $red;
    margin-left: 3px;
}
.settings{
    display: flex;
    align-items: center;
}
.settingsImg{
    width: 20px;
    height: 18px;
    background: url("../../img/header/settings.svg");
    margin-right: 10px;
}