$black: #2A2A2A;
$black-light: #565555;
$grey: #565555;
$grey-light:  #999999;
$another-grey-light: #BBBBBB;
$blue: #0085FF;
$white: #FFFFFF;
$green: #41BFB0;
$violet: #5241BF;
$red: #C65252;

@mixin common-text-style{
  font-family: SF-Pro-Display-Regular, sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: $black-light;
  @media only screen and (max-width: 426px) {
    font-size: 12px;
    line-height: 14px;
  }
}
@mixin common-h2{
  font-family: SF-Pro-Display-Bold, sans-serif;
  font-size: 16px;
  line-height: 149%;
  color: $black;
}
@mixin common-h3{
  font-family: SF-Pro-Display-Regular, sans-serif;
  font-size: 16px;
  line-height: 149%;
}
@mixin common-h4{
  font-family: SF-Pro-Display-Medium, sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: $black;
  @media only screen and (max-width: 426px) {
    font-size: 12px;
    line-height: 14px;
  }
}
@mixin common-h5{
  font-family: SF-Pro-Display-Regular, sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: $black;
}
@mixin common-h6{
  font-size: 14px;
  line-height: 17px;
  color: $another-grey-light;
}
