@import "../../../styles/variables";
.userList{
  padding: 20px 36px;
  transition: 1s;
  @media only screen and (max-width: 1024px) {
    display: none;
  }
}
.title{
  white-space: nowrap;
}
.list{
  margin-top: 20px;
}
.listItem{
  margin-bottom: 22px;
  white-space: nowrap;
  &:last-child{
    margin-bottom: 0;
  }
}
.link{
  color: $blue;
  text-decoration-line: underline;
}